@font-face {
  font-family: "Montserrat";
  src: url("../fonts/Montserrat-SemiBold.woff2") format("woff2"), url("../fonts/Montserrat-Montserrat-SemiBold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
}

@font-face {
  font-family: "Montserrat";
  src: url("../fonts/Montserrat-Montserrat-Regular.woff2") format("woff2"), url("../fonts/Montserrat-Montserrat-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
}

@font-face {
  font-family: "Montserrat";
  src: url("../fonts/Montserrat-Montserrat-Bold.woff2") format("woff2"), url("../fonts/Montserrat-Montserrat-Bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
}

* {
  box-sizing: border-box;
  margin: 0;
}

.body {
  font-family: "Montserrat", "Arial", sans-serif;
  min-width: 375px;
}

.container {
  max-width: 1500px;
  min-width: 375px;
  margin: 0 auto;
  padding: 0 30px;
}

.container-less {
  max-width: 1080px;
  min-width: 375px;
  margin: 0 auto;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  overflow: hidden;
}

.header {
  display: grid;
  grid-template-columns: 1fr min-content;
  grid-template-rows: min-content min-content;
  position: relative;
}

.header__main {
  grid-column: span 2;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #f5f5fa;
  margin: 0 -30px;
  padding: 0 15px;
}

.header__lang {
  display: flex;
  list-style-type: none;
  margin-right: 6px;
  padding: 10px 0;
}

.header__logo {
  display: flex;
  padding-top: 15px;
  padding-bottom: 10px;
  margin-left: -15px;
}

.header__menu-list {
  display: none;
  list-style: none;
  position: absolute;
  width: 375px;
  left: -30px;
  padding: 12px 12px 0;
}

.header__com {
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  text-align: right;
  color: #b3b3ba;
}

.header__menu {
  margin-right: -15px;
}

.mobile-btn {
  cursor: pointer;
  background-color: transparent;
  border: none;
  padding: 0;
}

.mobile-btn:active {
  background: #e6e6f0;
  border-radius: 8px;
}

.menu-btn-black path {
  transition: transform 0.25s;
}

.menu-btn-active path {
  transition: transform 0.25s;
}

.active path:nth-of-type(1) {
  transform: rotate(45deg);
  transform-origin: 12px 9px;
}

.active path:nth-of-type(2) {
  display: none;
}

.active path:nth-of-type(3) {
  transform: rotate(-45deg);
  transform-origin: 3px 20px;
}

.btn-color:nth-child(-n + 3) path {
  stroke: #ec6f5d;
}

.adress {
  background: url(../img/point.svg) no-repeat left, url(../img/point.svg) no-repeat right;
  padding-left: 12px;
  padding-right: 12px;
}

.adress,
.fb {
  display: none;
}

.fb {
  background: url(../img/point.svg) no-repeat left center;
  padding-left: 12px;
  cursor: pointer;
}

.fb:hover path {
  fill: #f0145a;
}

.link {
  text-decoration: none;
}

.link-lang {
  font-weight: 700;
  font-size: 12px;
  line-height: 15px;
  text-align: center;
  letter-spacing: 1px;
  color: #b3b3ba;
  text-transform: uppercase;
  margin: 5px 7px;
}

.link-lang:active {
  border-radius: 8px;
  background: #f0145a;
  padding: 5px 7px;
  color: #ffffff;
}

.link-lang:hover {
  border-radius: 8px;
  background: #f0145a;
  padding: 5px 7px;
  color: #ffffff;
}

.tel {
  font-weight: 700;
  margin: 0 6px;
}

.logo-text {
  font-weight: 400;
  font-size: 36px;
  line-height: 44px;
  color: #323264;
  background: url(../img/logo-slash.svg) no-repeat top left;
  padding-left: 25px;
}

.logo {
  padding-right: 4px;
}

.list-item {
  margin-bottom: 15px;
  display: block;
  font-weight: 700;
  font-size: 24px;
  line-height: 29px;
  text-align: center;
  color: #24a3ff;
  background: #f5f5fa;
  border-radius: 12px;
}

.list-item:active {
  background-color: #e6e6f0;
}

.item {
  font-weight: 700;
  font-size: 24px;
  line-height: 29px;
  text-align: center;
  color: #24a3ff;
}

.item:hover {
  color: #f0145a;
  border-bottom: 3px solid #f0145a;
}

@media (min-width: 768px) {
  .header {
    position: static;
    padding-top: 50px;
    grid-template-columns: 1fr;
    grid-template-rows: repeat(3, min-content);
  }
  .header__main {
    grid-column: 1;
    justify-content: flex-start;
    background: none;
    padding: 0 0 0 30px;
  }
  .header__menu-list {
    display: -webkit-box;
    padding: 0;
    list-style-type: none;
    padding-left: 30px;
    position: static;
  }
  .header__lang {
    padding: 0;
  }
  .header .adress,
  .header .fb {
    display: flex;
  }
  .header__logo {
    padding-left: 30px;
    padding-top: 30px;
    padding-bottom: 30px;
    margin-left: 0;
  }
  .mobile-btn {
    display: none;
  }
  .list-item {
    padding-right: 15px;
    background: none;
    margin-bottom: 0;
    height: 25px;
  }
  .logo svg {
    width: 130px;
    height: 60px;
  }
  .item {
    font-weight: 700;
    font-size: 16px;
    line-height: 20px;
    text-align: right;
  }
  .item:hover {
    color: #f0145a;
    border-bottom: 3px solid #f0145a;
  }
  .list-item:active {
    background: none;
  }
}

@media (min-width: 1024px) {
  .header {
    padding-right: 30px;
    grid-template-columns: repeat(2, max-content);
    grid-template-rows: repeat(2, min-content);
    justify-content: space-between;
  }
  .header__main {
    grid-column: 0;
    align-items: flex-start;
    grid-column-start: 2;
    grid-row-start: 1;
    margin-left: auto;
    align-self: center;
    padding-bottom: 17px;
  }
  .header__menu-list {
    position: static;
    width: auto;
  }
  .header__logo {
    grid-column-start: 1;
    grid-row-start: 1;
    grid-row-end: 3;
    padding-top: 0;
    padding-bottom: 0;
  }
  .header__menu {
    grid-column-start: 2;
    grid-row-start: 2;
    margin-top: -8px;
    margin-left: auto;
    margin-right: 0;
    align-self: flex-start;
  }
  .header__lang {
    align-self: center;
  }
  .header__com {
    align-self: center;
  }
  .item:hover {
    color: #24a3ff;
    border-bottom: 3px solid #24a3ff;
  }
  .item:active {
    color: #f0145a;
    border-bottom: 3px solid #f0145a;
  }
  .logo-text {
    width: 150px;
  }
}

