  .footer svg {
    grid-row-start: 2;
    grid-column: 1;
  }
  .footer__mail {
    grid-row-start: 2;
    padding: 0 0 0 16px;
    background: url(../img/point.svg) no-repeat left center;
    grid-column: 1 / 2;
    margin-left: 175px;
  }
  .footer__sign {
    padding-top: 40px;
    grid-column: 1;
    grid-row: 3;
    margin-left: auto;
    margin-right: auto;
  }
  .link-map {
    grid-column: 1 / 1;
    padding: 0 0 0 16px;
    background: url(../img/point.svg) no-repeat center left;
    grid-row-start: 1;
    margin-left: 188px;
    margin-bottom: 20px;
