.text-date {
  font-weight: 700;
  font-size: 12px;
  line-height: 15px;
  letter-spacing: 1px;
  text-transform: uppercase;
  color: #b3b3ba;
}

.text-regular {
  font-weight: 400;
  font-size: 14px;
  line-height: 22px;
  color: #000000;
}

@media (min-width: 768px) {
  .header__menu-list {
    margin: 0;
  }
  .header__menu-list .list-item:nth-child(5) {
    padding-right: 0;
  }
  .header__main {
    background: none;
    margin: 0;
  }
  .header__logo {
    padding-bottom: 30px;
  }
  .header__lang-pos {
    padding: 0;
  }
  .logo {
    width: 130px;
    height: 60px;
  }
  .tel {
    margin: 0 6px;
  }
  .first-screen {
    background-size: 893px 1024px, auto;
  }
  .first-screen-item {
    font-weight: 700;
    font-size: 16px;
    line-height: 20px;
    color: #ffffff;
  }
  .first-menu__caption {
    padding: 274px 0 417px 0;
  }
  .first-menu__scroll-btn {
    margin-bottom: 90px;
  }
  .first-nav__flex {
    margin-left: 30px;
  }
  .first-nav__item {
    font-weight: 700;
    font-size: 38px;
    line-height: 46px;
  }
  .first-text {
    padding-top: 60px;
    padding-right: 30px;
  }
  .first-text__caption-pos {
    padding: 10px 0 60px 30px;
  }
  .services {
    padding-top: 60px;
    padding-bottom: 0;
  }
  .services__card {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }
  .services__box {
    height: 100%;
    padding: 0 30px;
    padding-bottom: 60px;
  }
  .services__text {
    padding-bottom: 0;
  }
  .services__wrap {
    width: 50%;
  }
  .services__link {
    padding-left: 30px;
  }
  .caption-tabl {
    font-weight: 700;
    font-size: 38px;
    line-height: 46px;
  }
  .block-tabl {
    display: flex;
  }
  .main-card-bottom {
    height: auto;
    margin-bottom: 30px;
  }
  .img-last-tablet {
    height: 200px;
    width: 648px;
    object-fit: cover;
    border-radius: 8px 8px 0 0;
  }
  .footer {
    padding: 30px;
  }
  .link-map {
    margin-left: 197px;
    width: 78px;
    white-space: nowrap;
  }
}

@media (min-width: 1024px) {
  .header__logo {
    padding-bottom: 0;
    height: 60px;
    align-self: center;
  }
  .header__main {
    margin-left: auto;
  }
  .first-screen {
    background-size: 670px 768px, auto;
    margin-bottom: 80px;
  }
  .first-menu__caption {
    padding: 250px 289px;
    white-space: nowrap;
  }
  .first-nav {
    border-bottom: none;
    border-right: 4px solid rgba(255, 255, 255, 0.3);
    padding-right: 60px;
    padding-bottom: 0;
  }
  .first-nav__item {
    margin-right: 0;
  }
  .first-nav__list {
    flex-direction: column;
    align-items: flex-end;
  }
  .first-nav__flex {
    display: flex;
    margin-left: 30px;
  }
  .first-nav__stroke {
    margin-bottom: 30px;
  }
  .first-text {
    padding-top: 0;
    margin-left: 60px;
  }
  .first-text__caption-pos {
    padding-left: 30px;
  }
  .caption-text {
    font-weight: 700;
    font-size: 30px;
    line-height: 37px;
    color: #ffffff;
  }
  .services__box {
    width: auto;
  }
  .services__wrap {
    width: 33.333%;
  }
  .caption-tabl {
    width: 444px;
  }
  .img-last-tablet {
    width: auto;
    height: auto;
  }
  .footer {
    padding: 30px 60px;
  }
}

@media (min-width: 1440px) {
  .header {
    padding-right: 30px;
  }
  .first-screen {
    background-size: 697px 799px, auto;
  }
  .first-nav__flex {
    justify-content: center;
  }
  .about__start {
    width: 662px;
  }
  .first-menu__caption {
    padding: 266px 0 305px 0;
  }
  .first-text {
    align-self: center;
    width: 581px;
  }
  .caption-tabl {
    width: 860px;
  }
  .img-last-tablet {
    height: 200px;
  }
  .img-desktop {
    height: 200px;
    object-fit: cover;
    border-radius: 8px 8px 0 0;
  }
}

.main-content__card {
  background: #f5f5fa;
  border-radius: 0 0 12px 12px;
  padding: 30px;
}

.main-content__card a {
  margin-bottom: 5px;
}

.main-content__switcher {
  display: flex;
  justify-content: center;
  align-items: center;
  padding-top: 30px;
  padding-bottom: 60px;
}

.main-content__partners {
  display: flex;
  flex-direction: column;
}

.main-content__text {
  font-weight: 700;
  font-size: 16px;
  line-height: 20px;
  color: #b3b3ba;
  padding-top: 30px;
  padding-bottom: 6px;
}

.main-content__text-less {
  font-weight: 400;
  font-size: 14px;
  line-height: 22px;
  color: #b3b3ba;
}

.main-content__img {
  object-fit: cover;
  width: 315px;
  height: 500px;
  border-radius: 12px 12px 0 0;
}

@media (min-width: 768px) {
  .main-content__partners {
    padding: 0 30px;
  }
  .main-content__img {
    width: 648px;
  }
}

@media (min-width: 1024px) {
  .main-content__partners {
    flex-direction: row;
    margin: 0;
  }
  .main-content__img {
    width: 400px;
    height: 528px;
    border-radius: 12px 0px 0px 12px;
  }
  .main-content__card {
    border-radius: 0 12px 12px 0;
  }
  .main-content__card a {
    margin-left: 0;
  }
}

@media (min-width: 1440px) {
  .main-content__img {
    height: 344px;
  }
}

.publications {
  padding-top: 30px;
  background: #f5f5fa;
}

.publications__card {
  padding-bottom: 30px;
  border-bottom: 2px solid #e6e6f0;
}

.publications__caption {
  padding-top: 5px;
}

.publications__caption-blue {
  padding: 30px 30px 15px 30px;
}

.publications__main-card {
  display: flex;
  flex-direction: column;
  background: #ffffff;
  border-radius: 8px;
  margin-top: 30px;
}

.publications__text-date {
  padding-bottom: 15px;
  border-bottom: 4px solid #e6e6f0;
  margin: 0 30px;
}

.publications__text-regular {
  padding: 15px 30px 30px;
}

@media (min-width: 768px) {
  .publications {
    padding-left: 30px;
    padding-right: 30px;
  }
  .publications__card {
    display: flex;
    flex-wrap: wrap;
    margin: 0 -15px;
    border-bottom: none;
    padding-bottom: 0;
  }
  .publications__main-card {
    height: 100%;
  }
  .publications__wrap {
    width: 50%;
    padding: 0 15px;
  }
  .wrap-all {
    width: 100%;
    height: fit-content;
    padding-top: 30px;
    margin: 0 15px;
    padding-left: 0;
    padding-right: 0;
    border-bottom: 2px solid #e6e6f0;
  }
}

@media (min-width: 1024px) {
  .publications__wrap {
    width: 33.333%;
    margin: 0 -15px;
  }
  .publications__text-regular {
    padding-bottom: 30px;
  }
  .publications__main-card {
    height: 100%;
    margin-top: 0;
  }
  .publications__card {
    justify-content: space-between;
    margin: 0;
    padding-top: 30px;
    padding-bottom: 30px;
    border-bottom: 2px solid #e6e6f0;
  }
  .wrap-all {
    height: auto;
    padding-top: 0;
    margin: 0;
    border-bottom: none;
  }
}

.trust-us__cards {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.trust-us__block {
  width: 100%;
  text-align: center;
}

.trust-us__block:nth-child(1) {
  background: #1c4280;
  padding: 36px 0;
}

.trust-us__block:nth-child(2) {
  background: #143646;
  padding: 44px 0;
}

.trust-us__block:nth-child(3) {
  background: #f48b30;
  padding: 38px 0;
}

.trust-us__block:nth-child(4) {
  background: #9e4dc3;
  padding: 39px 0;
}

.trust-us__block:nth-child(5) {
  background: #fd9339;
  padding: 38px 0;
}

.trust-us__block:nth-child(6) {
  background: #e2e2e4;
  padding: 36px 0;
}

.trust-us__caption {
  font-weight: 700;
  font-size: 14px;
  line-height: 17px;
  letter-spacing: 1px;
  text-transform: uppercase;
  color: #323264;
  text-align: center;
  padding-bottom: 30px;
}

@media (min-width: 768px) {
  .trust-us__cards {
    flex-direction: row;
    flex-wrap: wrap;
  }
  .trust-us__block {
    display: flex;
    justify-content: center;
    width: 33.333%;
    height: 100%;
  }
}

@media (min-width: 1440px) {
  .trust-us__block {
    width: 25%;
  }
}
